import React, { Component } from 'react'
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, {
    Search,
  } from 'react-bootstrap-table2-toolkit';
import { connect } from 'react-redux';
import { Field, reduxForm } from 'redux-form';
import Swal from 'sweetalert2';
import {  getcustomerInduk, getcustomerList } from '../../actions/customerAction';
import { berhasil, gagal } from '../../Alert';
import { AxiosMasterDelete } from '../../Axios';
import { renderFieldSelect } from '../../renderField';

  const { SearchBar } = Search;

class FormTambahInduk extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            columns : [
                {
                    dataField: "nama_customer",
                    text: "Nama Customer",
                    headerStyle: () => {
                      return { width: "15%" };
                    },
                  },
                  {
                    dataField: "tanggal_daftar",
                    text: "Tanggal Daftar",
                    headerStyle: () => {
                      return { width: "15%" };
                    },
                  },
                  {
                    dataField: "nama_toko",
                    text: "Nama Toko",
                    headerStyle: () => {
                      return { width: "15%" };
                    },
                  },
                  {
                    dataField: "level_customer",
                    text: "Level Customer",
                    headerStyle: () => {
                      return { width: "10%" };
                    },
                  },
                  {
                    dataField: "Aksi",
                    text: "Aksi",
                    csvExport: false,
                    formatter: (rowContent, row) => {
                      return (
                        <div className="row justify-content-center">
                          <button
                            type="button"
                            className="btn btn-danger mr-2"
                            onClick={() => this.hapus(row.kode_customer)}
                            data-tut="reactour_3"
                          >
                            <i className="fa fa-times mr-1"></i>
                            Delete 
                          </button>
                        </div>
                      );
                    },
                  },
            ]
         }
    }

    componentDidMount(){
        this.props.dispatch(getcustomerList())
    }

    hapus( kode) {
        Swal.fire({
          title: "Anda Yakin ?",
          text: "Anda Akan Menghapus Data Ini ?",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yes, delete it!",
        }).then((result) => {
          if (result.isConfirmed) {
            AxiosMasterDelete(`customer/induk/${this.props.customer}&${kode}`)
              .then((res) =>
                berhasil("Data Berhasil Dihapus!")
                  .then(() => this.props.dispatch(getcustomerInduk(this.props.customer)))
              )
              .catch((err) =>
                gagal("Data Gagal Dihapus!")
              );
          }
        });
      }
    render() { 
        return ( 
        <form onSubmit={this.props.handleSubmit}>
            <div className="col-lg-12 row">
                <div className="col-lg-3"></div>
                <div className="col-lg-4">
                        <Field
                        label="Pilih Kode Customer"
                        name="kodeCustomer"
                        component={renderFieldSelect}
                        options={this.props.listCustomer.filter((fil) => fil.level_customer === this.props.level).map((data) => {
                            return {
                                value : data.kode_customer,
                                name :data.nama_customer
                            }
                        })}
                        />
                </div>
                <div className="col-lg-2">
               <div className="form-group">
                   <label htmlFor="">&nbsp;</label>
               <button
                        type="submit"
                        className="btn btn-primary form-control"
                        disabled={this.props.onSend}
                        >
                        {this.props.onSend ? (
                            <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                            ></span>
                        ) : null}
                        Tambah
                        <i className="fa fa-paper-plane ml-3"></i>
                        </button>
               </div>
                </div>
                <div className="col-lg-3"></div>
                <ToolkitProvider
                      keyField="kode_customer"
                      data={this.props.listCustomerInduk || []}
                      columns={this.state.columns}
                      search
                      >
                      {(props) => (
                          <div className="text-left mt-5 mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                              />
                          </div>
                          <hr />
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                            />
                        </div>
                      )}
                    </ToolkitProvider>
            </div>
            </form> 
         );
    }
}
 
FormTambahInduk = reduxForm({
    form:"FormTambahInduk"
})(FormTambahInduk)
export default connect((state) => {
    return {
        listCustomer : state.provinsi.customer,
        listCustomerInduk : state.provinsi.customerInduk
    }
})(FormTambahInduk);